.bn-banner {
	background-image: url(/mycvproject/static/media/background.fab1e598.jpg);
	min-height: 220px;
	position: relative
}

.bn-author-name {
	color: #fdfdfd !important;
	font-weight: bold;
	text-align: center;
	text-shadow: 5px 2px 1px #2b2012;
	letter-spacing: 1px;
}

.bn-logos-left {
	position: absolute;
	bottom: 0;
	left: 0;
	margin-top: 15px;
	padding-left: 5px !important;
}

.bn-logos-right {
	position: absolute;
	bottom: 0;
	right: 0;
	margin-bottom: 10px;
	text-align: right;
}

.bn-logo {
	margin: 0 20px 0 0;
}

.bn-circle-img {
	width:40px;
	height:40px;
}

.bn-printer {
	background: none;
	border: none;
	outline:none;
	font-size: 25px;
	color: black;
}

.bn-center {
	text-align: center;
}

.cr-print-header {
	text-align: center;
}

.cr-title-space {
	padding-bottom: 30px;
}

.cr-company-logo {
	text-align: right;
	margin-left: auto;
	margin-right: auto;
	display: block;
}

.cr-pt-logo {
	height: 25px;
}

.cr-sg-logo {
	height: 35px;
}

.cr-gh-logo {
	height: 45px;
}

.cr-space {
	padding-bottom: 10px;
}

.cr-desc {
	font-size: 16px;
	padding-bottom: 5px;
}

.cr-title {
	font-size: 30px;
}

.cr-line {
	margin-top: 20px;
	margin-bottom: 20px;
}

.cr-page-break {
	page-break-before: always;
}

.ed-print-header {
	text-align: center;
}

.ed-title-space {
	padding-bottom: 30px;
}

.ed-company-logo {
	text-align: right;
	margin-left: auto;
	margin-right: auto;
	display: block;
}

.ed-rgu-logo {
	height: 60px;
}

.ed-thes-logo {
	height: 90px;
}

.ed-space {
	padding-bottom: 10px;
}

.ed-desc {
	font-size: 16px;
	padding-bottom: 5px;
}

.ed-title {
	font-size: 30px;
}

.ed-line {
	margin-top: 20px;
	margin-bottom: 20px;
}

.pr-print-header {
	text-align: center;
	page-break-before: always;
}

.pr-title-space {
	padding-bottom: 30px;
}

.pr-octo-logo {
	width:40px;
	height:40px;
}

.pr-garmin-logo {
	width:40px;
	height:40px;
}

.pr-chrome-logo {
	width:50px;
	height:55px;
}

.pr-space {
	padding-bottom: 10px;
}

.pr-desc {
	font-size: 16px;
	padding-bottom: 5px;
}

.pr-title {
	font-size: 30px;
}

.pr-line {
	margin-top: 20px;
	margin-bottom: 20px;
}

.pr-label {
	padding-right: 5px;
}

.pr-label span {
	background-color: lightblue;
	color: black;
}

.nav-pills>li>a {
	border-radius: 50px;
	margin: 0 10px 0 10px;
	color: black;
	font-weight: bold;
	font-size: 17px;
}

.nav-pills>li.active>a, .nav-pills>li.active>a:focus, .nav-pills>li.active>a:hover {
	background-color: black;
}

.tabs-space {
	padding-bottom: 80px;
}

.App {
	text-align: center;
}

.font {
	font-family: 'New Century Schoolbook', serif;
	text-align: justify;
}

@page {
	size: A3;
	margin-left: -5cm;
	margin-right: -5cm;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

